const fs = require('fs');
const path = require('path');
const MathServer = require('./impl/math_server');

async function main() {
    // Make IO streams explicit
    const io = {
        inputStream: process.stdin,
        outputStream: process.stdout
    };

    // Required configuration with no defaults
    const serverConfig = {
        baseDir: process.env.BASE_DIR || path.resolve(__dirname, '..'),
        logDir: process.env.LOG_DIR || '_logs',
        debugDir: process.env.DEBUG_DIR || '_debug',
        ...io
    };

    const server = new MathServer(serverConfig);

    try {
        await server.start();
        
        // Handle process signals
        const signals = ['SIGINT', 'SIGTERM', 'SIGHUP'];
        signals.forEach(signal => {
            process.on(signal, async () => {
                console.log(`\nReceived ${signal}. Shutting down...`);
                await server.stop();
                process.exit(0);
            });
        });

        // Handle uncaught errors
        process.on('uncaughtException', async (error) => {
            console.error('Uncaught Exception:', error);
            await server.stop();
            process.exit(1);
        });

        process.on('unhandledRejection', async (reason, promise) => {
            console.error('Unhandled Rejection at:', promise, 'reason:', reason);
            await server.stop();
            process.exit(1);
        });

    } catch (error) {
        console.error('Failed to start server:', error);
        process.exit(1);
    }
}

// Start the server
if (require.main === module) {
    main().catch(error => {
        console.error('Fatal error:', error);
        process.exit(1);
    });
}

module.exports = main;
